.feedback-card {
    width: 75%;
    background: rgba($color-white,  0.75);
    box-shadow: $box-shadow;
    margin: 0 auto;
    padding: 6rem;
    transform: skewX(-15deg);
    & > * {
        transform: skewX(15deg); }
    &__img {
        width: 15rem;
        height: 15rem;
        // background-color: green
        float: left;
        border-radius: 100rem;
        shape-outside: circle(50% at 50% 50%);
        -webkit-shape-outside: circle(50% at 50% 50%);
        margin-right: 4rem;
        overflow: hidden;
        position: relative;
        img {
            height: 100%;
            transform: scale(1.2);
            transition: all .5s; } }
    &__name {
        text-align: center;
        font-size: 2rem;
        letter-spacing: .2rem;
        font-weight: 400;
        color: $color-white;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -0%);
        transition: all .5s;
        opacity: 0; }
    &:hover &__name {
        transform: translate(-50%, -50%);
        opacity: 1; }
    &:hover img {
        transform: scale(1);
        filter: blur(.3rem) brightness(70%); } }


